/* Side Menu
=================================*/

.side-menu-open {
  overflow: hidden !important;
}

.side-menu-nav {
  position: fixed;
  z-index: 101;
  top: 0;
  overflow: hidden;
  width: 22.25rem;
  height: 100%;
  color: #212121;
  background-color: #fff;
  padding: 20px;
}

.side-menu-brand {
  font-size: 1.5rem;
  font-weight: bold;
  line-height: 3.75rem;
  display: block;
  padding-right: .75rem;
  padding-left: .75rem;
  text-decoration: none;
  color: #212121;
  margin-top: 0px;
}

.side-menu-wrap {
  margin: 50px 0;
  padding: 0;
  list-style: none;
}

.side-menu-list-item {
  font-size: 16px;
  display: block;
  padding: .75rem;
  text-decoration: none;
  color: #757575;
}

.side-menu-list-item:hover, .side-menu-list-item:focus {
  color: #212121;
  background-color: transparent;
}

/*! overlay */

.side-menu-overlay {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  display: none;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .2);
}

.side-menu-open .side-menu-overlay {
  display: block;
}

/*!------------------------------------*\
    Top
\*!------------------------------------*/

.side-menu-top .side-menu-nav {
  top: -100%;
  left: 0;
  width: 100%;
  height: auto;
  max-height: 100%;
  transition: top .6s cubic-bezier(0.190, 1.000, 0.220, 1.000);
}

.side-menu-top.side-menu-open .side-menu-nav {
  top: 0;
}

.side-menu-top .side-menu-hamburger,
.side-menu-top.side-menu-open .side-menu-hamburger {
  right: 0;
}

/*!------------------------------------*\
    Left
\*!------------------------------------*/

.side-menu-left .side-menu-nav {
  left: -22.25rem;
  transition: left .6s cubic-bezier(0.190, 1.000, 0.220, 1.000);
}

.side-menu-left.side-menu-open .side-menu-nav,
.side-menu-left .side-menu-hamburger,
.side-menu-left.side-menu-open .side-menu-navbar .side-menu-hamburger {
  left: 0;
}

.side-menu-left.side-menu-open .side-menu-hamburger {
  left: 22.25rem;
}

/*!------------------------------------*\
    Right
\*!------------------------------------*/

.side-menu-right .side-menu-nav {
  right: -22.25rem;
  transition: right .6s cubic-bezier(0.190, 1.000, 0.220, 1.000);
}

.side-menu-right .side-menu-hamburger {
    margin-right: 25px;
    margin-left: 0;
}

.side-menu-right.side-menu-open .side-menu-nav,
.side-menu-right .side-menu-hamburger,
.side-menu-right.side-menu-open .side-menu-navbar .side-menu-hamburger {
  right: 0;
}

.side-menu-right.side-menu-open .side-menu-hamburger {
  right: 22.25rem;
}

/*!------------------------------------*\
    Hamburger
\*!------------------------------------*/

.side-menu-hamburger {
    position: fixed;
    z-index: 104;
    top: 25px;
    display: block;
    box-sizing: content-box;
    width: 2.6rem;
    padding: 0;
    padding-top: 18px;
    padding-right: 1.75rem;
    padding-bottom: 31px;
    padding-left: 1.75rem;
    transition: all .6s cubic-bezier(0.190, 1.000, 0.220, 1.000);
    transform: translate3d(0, 0, 0);
    border: 0;
    outline: 0;
    background-color: #fff;
    margin-left: 25px;
	box-shadow: 2px 2px 29px 5px rgba(31, 31, 31, 0.07);
}

.side-menu-hamburger:hover {
  cursor: pointer;
  background-color: #fff;
}

.side-menu-hamburger-icon {
  position: relative;
  display: block;
  margin-top: 10px;
}

.side-menu-hamburger-icon,
.side-menu-hamburger-icon:before,
.side-menu-hamburger-icon:after {
  width: 100%;
  height: 2px;
  transition: all .6s cubic-bezier(0.190, 1.000, 0.220, 1.000);
  background-color: #222;
}

.side-menu-hamburger-icon:before,
.side-menu-hamburger-icon:after {
  position: absolute;
  top: -8px;
  left: 0;
  content: ' ';
}

.side-menu-hamburger-icon:after {
  top: 8px;
}

.side-menu-open .side-menu-hamburger-icon {
  background-color: transparent;
}

.side-menu-open .side-menu-hamburger-icon:before,
.side-menu-open .side-menu-hamburger-icon:after {
  top: 0;
}

.side-menu-open .side-menu-hamburger-icon:before {
  transform: rotate(45deg);
}

.side-menu-open .side-menu-hamburger-icon:after {
  transform: rotate(-45deg);
}

/*!------------------------------------*\
    accessibility
\*!------------------------------------*/

/*!
 * Only display content to screen readers
 * See: http://a11yproject.com/posts/how-to-hide-content
 */

.sr-only {
  position: absolute;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}

/*!
 * Use in conjunction with .sr-only to only display content when it's focused.
 * Useful for "Skip to main content" links; see http://www.w3.org/TR/2013/NOTE-WCAG20-TECHS-20130905/G1
 * Credit: HTML5 Boilerplate
 */

.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  overflow: visible;
  clip: auto;
  width: auto;
  height: auto;
  margin: 0;
}

